.form-section {
  text-align: center;
  width: 100vw;
  background: #064271;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: block;
}

input {
  height: 25px;
  width: 200px;
  border: 3px solid #838181;
  border-radius: 16px;
  padding: 0 15px;
}

.search-button {
  height: 31px;
  width: 75px;
  border-radius: 16px;
}

.hidden {
  display: none;
}

.load-more-button-wrapper {
  text-align: center;
  justify-content: center;
  display: block;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 20px;
  display: grid;
}

img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 16px 16px 0 0;
}

.photo-card {
  border: 1px solid #838181;
  border-radius: 20px;
  box-shadow: 3px 2px 10px -1px #47425a;
}

.info {
  text-align: center;
  grid-template-columns: repeat(2, 2fr);
  justify-content: center;
  display: grid;
}

.info-item {
  border-radius: 50px;
  margin: 5px 25px 10px;
  box-shadow: 2px 2px 10px -2px #47425a;
}

